import React from 'react';
import clsx from "clsx";
import { Grid, Button, Hidden, Tooltip, Zoom } from "@material-ui/core";
import { Add, Delete } from "@material-ui/icons";
import { useTheme } from "@material-ui/core/styles";
import CommonStyle from "./CommonStyle";
import SearchBar from "./SearchBar";

const ActionBar = (props) => {
    const theme = useTheme();
    const classes = CommonStyle(theme);
    
    return (
        <Grid container item xs={12} lg={6} className={clsx(classes.actionbar, classes.removePadding)}>
            <Grid item xs={8} lg={6} style={{ textAlign: "left" }} >
                <SearchBar classes={classes} search={props.search} error={props.error} data={props.data} onChange={props.onChange} setActive={props.setActive} filterData={props.filterData}
                    wannaCreateNew={props.wannaCreateNew} />
            </Grid>
            <Grid item xs={2} lg={3}>
                <Tooltip title="Add Contact" arrow TransitionComponent={Zoom}>
                    <span className={clsx(classes.btn, { [classes.cursorDisabled]: props.wannaCreateNew })}>
                        <Button
                        variant="contained"
                        className={clsx(classes.button, classes.bgGradient, classes.btnAdd)}
                        size="large"
                        fullWidth
                        disabled={props.wannaCreateNew}
                        onClick={() => props.handleAdd(true)} >

                        <Add /> <Hidden smDown>Add</Hidden>

                    </Button> </span>
                </Tooltip>
            </Grid>
            <Grid item xs={2} lg={3}>
                <Tooltip title={props.someSelected ? "Delete Contacts" : "Select Contacts to Delete"} arrow TransitionComponent={Zoom}>
                    <span className={clsx(classes.btn, { [classes.cursorDisabled]: !props.someSelected })}>
                        <Button
                            variant="contained"
                            // style={{paddingRight:"16px"}}
                            className={clsx(classes.button, classes.bgGradient, classes.btnDelete)}
                            size="large"
                            fullWidth
                            onClick={props.handleDelete}
                            disabled={!props.someSelected || props.wannaCreateNew}
                        > <Delete /> <Hidden smDown>Delete&nbsp;</Hidden>
                        </Button>
                    </span>
                </Tooltip>
            </Grid>
        </Grid >

    )
}

export default ActionBar;
